<template>
  <div class="custom-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">网站防护</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/protection/' + domain }">防护配置</el-breadcrumb-item>
        <el-breadcrumb-item>IP黑名单</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-row class="text-align-right">
          <div class="data-search-input">
            <el-input placeholder="请输入关键词进行搜索" prefix-icon="el-icon-search" v-model="dataSearch"
              @input="onChangeSearch">
            </el-input>
            <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
          </div>
          <a class="el-button el-button--primary" :href="'/#/flow-black-ip-edit/' + domain + '/' + ip">新增黑名单</a>
        </el-row>
        <div class="demo-block">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="ip" label="IP地址"></el-table-column>
            <el-table-column prop="detail" label="描述"></el-table-column>
            <el-table-column prop="expire_time" label="过期时间"></el-table-column>
            <el-table-column prop="block_action" label="处置动作">
              <template #default="scope">
                <el-tag v-if="scope.row.block_action == 'reject_response'" size="small" type="danger">拒绝响应</el-tag>
                <el-tag v-if="scope.row.block_action == 'block'" size="small" type="warning">阻断请求</el-tag>
                <el-tag v-if="scope.row.block_action == 'watch'" size="small">观察模式</el-tag>
                <el-tag v-if="scope.row.block_action == 'bot_check'" size="small" type="success">人机识别：
                  <span v-if="scope.row.action_value == 'standard'">标准</span>
                  <span v-if="scope.row.action_value == 'slipper'">滑块</span>
                  <span v-if="scope.row.action_value == 'image'">图片验证码</span>
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text">配置
                </el-button>
                <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                  <p>确定删除吗？</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                    </el-button>
                  </div>
                  <template #reference>
                    <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                  </template>
                </el-popover>
              </template>
            </el-table-column>

          </el-table>
          <el-pagination background layout="prev, pager, next" :total="tableTotal" v-model:current-page="currentPage"
            :page-size="50" @current-change="onCurrentChange" />
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      tableData: [],
      domain: "",
      ip: "new",
      dataSearch: "",
      currentPage: 1,
      tableTotal: 0,
    };
  },
  computed: {
    rules() {
      return {
      }
    }
  },
  mounted() {
    const route = useRoute()
    this.domain = route.params.domain;
    //this.getData();
    this.onCurrentChange(this.currentPage);
  },
  methods: {
    getData(page) {
      var t = this;
      var postUrl = "/waf/waf_get_flow_black_ip_list";
      var oData = { domain: t.domain,page:page };
      
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
          t.tableTotal = response.data.count;
          t.currentPage = response.data.now_page;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    handleEdit(data) {
      window.location.href = "/#/flow-black-ip-edit/" + this.domain + '/' + data.ip;
    },
    handleDelete(data) {
      var t = this;
      t.loading = true;
      var postUrl = "/waf/waf_del_flow_black_ip";
      var oData = {
        domain: t.domain,
        ip: data.ip,
      };   
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
    onCurrentChange() {
      if (this.dataSearch == "") {
        this.getData(this.currentPage)
      } else {
        this.onChangeSearch()
      }
    },
    onChangeSearch() {
      var t = this;
      if (t.dataSearch) {
        JXAjax(
          "post",
          "/waf/waf_search_flow_black_ip",
          { page: t.currentPage, search_value: t.dataSearch, domain: t.domain },
          function (response) {
            t.loadingPage = false;
            t.tableData = response.data.message;
            t.currentPage = response.data.now_page;
            t.tableTotal = response.data.count;
            t.tableData.forEach(item => {
              item.isVisiblePopover = false;
            })
          },
          function () {
            t.loadingPage = false;
          },
          "no-message"
        );
      } else {
        t.getData(1)
      }

    },
  },
};
</script>

<style>
.custom-wrap {
  /* max-width: 800px;
  min-width: 400px; */
}
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}

.custom-wrap .rule-matchs-content .match-box-title {
  display: inline-block;
  text-align: left;
  font-size: 14px;
  color: #909399;
  padding: 0 5px;
  box-sizing: border-box;
}
.custom-wrap .rule-matchs-content .match-box-title:before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
.custom-wrap .match-inline-block {
  width: 192px;
}
.custom-wrap .match-inline-block-small {
  width: 150px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
</style>
